@import "../Main.scss";
.wrapper{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 3vh 2vw;
   
}

// .mobileWrapper{

// }
.image{
    position: absolute;
    width: 100%;
    // height: 85%;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    z-index: 1;
    cursor: pointer;

}

.icon{
    position: relative;
    width: 70px;
    height: 70px;
    opacity: 0.5;
    mask-position: center;
    mask-repeat: no-repeat;
    mask-size: contain;
    background-color: $BrightColor;
    z-index: 2;
    transition: opacity 0.5s ease;
}
.text{
    position: absolute;
    font-family:"Assistant-Bold";
    color: white;
    font-size: 12pt;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    transition: opacity 0.5s ease;

}
.hidden{
    opacity: 0;
}
.reveale{
    opacity: 1;
}