@import "../Main.scss";

.home{
    position: relative;
    width: 100vw;
    height: 100vh;
    overflow:hidden;
    background-color: $backgroundColor;
    // background-color: $backgroundColor;
}

.websiteGate{
    position: absolute;
    width: 100vw;
    height: 100vh;
    background-color: $backgroundColor;
    background-position: center;
    background-size: 100vw;
    background-repeat: no-repeat;
    cursor: pointer;
    transition: opacity 2s ease 1s, z-index 0.2s ease 3.5s;
}


.video{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover;
      
}

.background{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: linear-gradient( to bottom, $backgroundColor ,rgba(22, 18, 18, 0.8) 30%, rgba(22, 18, 18, 0.8) 70%,$backgroundColor);
    mix-blend-mode: multiply;

}

.button1{
    position: absolute;
    width: 50px;
    height: 50px;
    top: 50%;
    left: 0;
    margin: 40px;
    transform: translateY(-50%);
    cursor: pointer;
    background-color: $BrightColor;
    mask-size: contain;
    mask-repeat: no-repeat;
    mask-position: center;
    opacity: 0.5;
    &:hover{
        opacity: 1;
    }
}
.button2{
    position: absolute;
    width: 50px;
    height: 50px;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    margin: 40px;
    cursor: pointer;
    background-color: $BrightColor;
    mask-size: contain;
    mask-repeat: no-repeat;
    mask-position: center;
    opacity: 0.5;
    &:hover{
        opacity: 1;
    }
}
.playPause{
    position: absolute;
    width: 50px;
    height: 50px;
    bottom: 5%;
    left: 50%;
    transform: translatex(-50%);
    margin: 10px;
    cursor: pointer;
    background-color: $BrightColor;
    mask-size: contain;
    mask-repeat: no-repeat;
    mask-position: center;
    opacity: 0.5;
    &:hover{
        opacity: 1;
    }
}
.path{
    stroke-dasharray: 2000;
    animation: dashIn 3s ease-in-out;
    fill:transparent;
    stroke:white;
    stroke-width:2;
}

.animationIn{
    animation: dashIn 3s ease-in-out;
}
.animationOut{
    stroke-dashoffset:-2000;
    animation: dashOut 3s ease-in-out;
}



@keyframes dashIn{
    from {
        stroke-dashoffset:2000;
    };
    to{
        stroke-dashoffset:0;
    }
}





@keyframes dashOut{
    from {
        stroke-dashoffset:0;
    };
    to{
        stroke-dashoffset:-2000;
    }
}






