@import "./../Main.scss";

.wrapper{
    position: relative;
    background-color: transparent;
    display: flex;
    align-items:flex-start;
    justify-content: space-between;
    flex-direction: column;
    margin-top: 20px;
}


.input{
    position: relative;
    width: 100%;
    background-color: $BrightColor;
    font-family: "Assistant-Light";
    text-indent: 6px;
    font-size:18pt ;
    cursor:auto;
    border-radius: 5px;
    color: $backgroundColor;
    
}
.title{
    position: relative;
    font-family: "Amiri-Italic";
    font-size: 16pt;
    color: $iconColor;
}
.error{
    position: absolute;
    right:0;
    bottom:-25px;
    color: red;
    font-family: "Assistant-SemiBold";
    animation: fadeIn 0.4s ease;

}

@keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
}

