@import "../Main.scss";

.home{
    position: relative;
    width: 100vw;
    background-color: $backgroundColor;
}


.textWrapper{
    display: flex;
    align-items: top;
    justify-content: space-between;
    padding:10vh 10vw;
    width: 80vw;
    background-color: $backgroundColor;
}

.text{
    font-family: "Assistant-SemiBold";
    white-space: pre-wrap;
    font-size: 17pt;
    line-height: 22pt;
    color: $BrightColor;
    position: relative;
    width: 22vw;
    text-align:justify;
    
}
.leikie{
    font-family: "Amiri-Italic";
    white-space: pre-wrap;
    font-size: 17pt;
    line-height: 22pt;
    color: $BrightColor;
    padding:10vh 10vw;
    width: 80vw;

}

.innerWrapper{
    width: 100%;
    height: 70%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    text-align:justify;

}
.hr{
    width: 80%;
    color: $BrightColor;
    opacity:0.2;
}
.header{
    width: 80%;
    color: $BrightColor;
    font-family: "Amiri-Italic";
    font-size: 30pt;
    text-align: start;



}
